<template>
  <div class="page_search">
	<div class="warp">
	  <div class="container">
		<div class="row">
		  <div class="col-12">
			<div class="card_result_search">
			  <div class="title">搜索结果</div>

				<!--{if filter.article}-->
				<!-- 文章搜索结果 -->
			  <list_result_search
				:list="result_article"
				title="留学资讯"
				source_table="article"
			  ></list_result_search>
				<!--{/if}-->


						  <list_result_search
				v-if="$check_action('/student_users/list', 'get')"
				:list="result_student_users_student_name"
				title="学生用户学生姓名"
				source_table="student_users"
			  ></list_result_search>
											  <list_result_search
				v-if="$check_action('/student_users/list', 'get')"
				:list="result_student_users_student_nationality"
				title="学生用户学生国籍"
				source_table="student_users"
			  ></list_result_search>
									  <list_result_search
				v-if="$check_action('/application_guide/list', 'get')"
				:list="result_application_guide_guide_title"
				title="申请指南指南标题"
				source_table="application_guide"
			  ></list_result_search>
								  <list_result_search
				v-if="$check_action('/application_guide/list', 'get')"
				:list="result_application_guide_guide_type"
				title="申请指南指南类型"
				source_table="application_guide"
			  ></list_result_search>
																		  <list_result_search
				v-if="$check_action('/about_us/list', 'get')"
				:list="result_about_us_article_title"
				title="关于我们文章标题"
				source_table="about_us"
			  ></list_result_search>
								  <list_result_search
				v-if="$check_action('/about_us/list', 'get')"
				:list="result_about_us_article_type"
				title="关于我们文章类型"
				source_table="about_us"
			  ></list_result_search>
															  <list_result_search
				v-if="$check_action('/professional_information/list', 'get')"
				:list="result_professional_information_name_of_institution"
				title="专业信息院校名称"
				source_table="professional_information"
			  ></list_result_search>
								  <list_result_search
				v-if="$check_action('/professional_information/list', 'get')"
				:list="result_professional_information_major_name"
				title="专业信息专业名称"
				source_table="professional_information"
			  ></list_result_search>
																											  <list_result_search
				v-if="$check_action('/application_for_studying_abroad/list', 'get')"
				:list="result_application_for_studying_abroad_name_of_institution"
				title="留学申报院校名称"
				source_table="application_for_studying_abroad"
			  ></list_result_search>
								  <list_result_search
				v-if="$check_action('/application_for_studying_abroad/list', 'get')"
				:list="result_application_for_studying_abroad_major_name"
				title="留学申报专业名称"
				source_table="application_for_studying_abroad"
			  ></list_result_search>
																																	  <list_result_search
				v-if="$check_action('/tuition_payment/list', 'get')"
				:list="result_tuition_payment_name_of_institution"
				title="学费缴纳院校名称"
				source_table="tuition_payment"
			  ></list_result_search>
								  <list_result_search
				v-if="$check_action('/tuition_payment/list', 'get')"
				:list="result_tuition_payment_major_name"
				title="学费缴纳专业名称"
				source_table="tuition_payment"
			  ></list_result_search>
																		  <list_result_search
				v-if="$check_action('/international_student_management/list', 'get')"
				:list="result_international_student_management_student_name"
				title="留学生管理学生姓名"
				source_table="international_student_management"
			  ></list_result_search>
											  <list_result_search
				v-if="$check_action('/international_student_management/list', 'get')"
				:list="result_international_student_management_student_nationality"
				title="留学生管理学生国籍"
				source_table="international_student_management"
			  ></list_result_search>
																		</div>
		  </div>
		</div>
	  </div>
	</div>
  </div>
</template>

<script>
import mixin from "../../mixins/page.js";
import list_result_search from "../../components/diy/list_result_search.vue";

export default {
  mixins: [mixin],
  data() {
	return {
	  "query": {
		word: "",
	  },
	  "result_article": [],
						"result_student_users_student_name":[],
											"result_student_users_student_nationality":[],
									"result_application_guide_guide_title":[],
								"result_application_guide_guide_type":[],
																		"result_about_us_article_title":[],
								"result_about_us_article_type":[],
															"result_professional_information_name_of_institution":[],
								"result_professional_information_major_name":[],
																											"result_application_for_studying_abroad_name_of_institution":[],
								"result_application_for_studying_abroad_major_name":[],
																																	"result_tuition_payment_name_of_institution":[],
								"result_tuition_payment_major_name":[],
																		"result_international_student_management_student_name":[],
											"result_international_student_management_student_nationality":[],
																};
  },
  methods: {
	/**
	 * 获取文章
	 */
	get_article() {
	  this.$get("~/api/article/get_list?like=0", { page: 1, size: 10, title: this.query.word }, (json) => {
		if (json.result) {
		  this.result_article = json.result.list;
		}
	  });
	},

				/**
	 * 获取student_name
	 */
	get_student_users_student_name(){
		let url = "~/api/student_users/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "student_name": this.query.word }, (json) => {
		  if (json.result) {
			var result_student_users_student_name = json.result.list;
			result_student_users_student_name.map(o => o.title = o['student_name'])
	  			this.result_student_users_student_name = result_student_users_student_name
		 	}
		});
	},
									/**
	 * 获取student_nationality
	 */
	get_student_users_student_nationality(){
		let url = "~/api/student_users/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "student_nationality": this.query.word }, (json) => {
		  if (json.result) {
			var result_student_users_student_nationality = json.result.list;
			result_student_users_student_nationality.map(o => o.title = o['student_nationality'])
	  			this.result_student_users_student_nationality = result_student_users_student_nationality
		 	}
		});
	},
							/**
	 * 获取guide_title
	 */
	get_application_guide_guide_title(){
		let url = "~/api/application_guide/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "guide_title": this.query.word }, (json) => {
		  if (json.result) {
			var result_application_guide_guide_title = json.result.list;
			result_application_guide_guide_title.map(o => o.title = o['guide_title'])
	  			this.result_application_guide_guide_title = result_application_guide_guide_title
		 	}
		});
	},
						/**
	 * 获取guide_type
	 */
	get_application_guide_guide_type(){
		let url = "~/api/application_guide/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "guide_type": this.query.word }, (json) => {
		  if (json.result) {
			var result_application_guide_guide_type = json.result.list;
			result_application_guide_guide_type.map(o => o.title = o['guide_type'])
	  			this.result_application_guide_guide_type = result_application_guide_guide_type
		 	}
		});
	},
																/**
	 * 获取article_title
	 */
	get_about_us_article_title(){
		let url = "~/api/about_us/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "article_title": this.query.word }, (json) => {
		  if (json.result) {
			var result_about_us_article_title = json.result.list;
			result_about_us_article_title.map(o => o.title = o['article_title'])
	  			this.result_about_us_article_title = result_about_us_article_title
		 	}
		});
	},
						/**
	 * 获取article_type
	 */
	get_about_us_article_type(){
		let url = "~/api/about_us/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "article_type": this.query.word }, (json) => {
		  if (json.result) {
			var result_about_us_article_type = json.result.list;
			result_about_us_article_type.map(o => o.title = o['article_type'])
	  			this.result_about_us_article_type = result_about_us_article_type
		 	}
		});
	},
													/**
	 * 获取name_of_institution
	 */
	get_professional_information_name_of_institution(){
		let url = "~/api/professional_information/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "name_of_institution": this.query.word }, (json) => {
		  if (json.result) {
			var result_professional_information_name_of_institution = json.result.list;
			result_professional_information_name_of_institution.map(o => o.title = o['name_of_institution'])
	  			this.result_professional_information_name_of_institution = result_professional_information_name_of_institution
		 	}
		});
	},
						/**
	 * 获取major_name
	 */
	get_professional_information_major_name(){
		let url = "~/api/professional_information/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "major_name": this.query.word }, (json) => {
		  if (json.result) {
			var result_professional_information_major_name = json.result.list;
			result_professional_information_major_name.map(o => o.title = o['major_name'])
	  			this.result_professional_information_major_name = result_professional_information_major_name
		 	}
		});
	},
																									/**
	 * 获取name_of_institution
	 */
	get_application_for_studying_abroad_name_of_institution(){
		let url = "~/api/application_for_studying_abroad/get_list?like=0";
					url = url+"&examine_state=已通过";
				this.$get(url, { page: 1, size: 10, "name_of_institution": this.query.word }, (json) => {
		  if (json.result) {
			var result_application_for_studying_abroad_name_of_institution = json.result.list;
			result_application_for_studying_abroad_name_of_institution.map(o => o.title = o['name_of_institution'])
	  			this.result_application_for_studying_abroad_name_of_institution = result_application_for_studying_abroad_name_of_institution
		 	}
		});
	},
						/**
	 * 获取major_name
	 */
	get_application_for_studying_abroad_major_name(){
		let url = "~/api/application_for_studying_abroad/get_list?like=0";
					url = url+"&examine_state=已通过";
				this.$get(url, { page: 1, size: 10, "major_name": this.query.word }, (json) => {
		  if (json.result) {
			var result_application_for_studying_abroad_major_name = json.result.list;
			result_application_for_studying_abroad_major_name.map(o => o.title = o['major_name'])
	  			this.result_application_for_studying_abroad_major_name = result_application_for_studying_abroad_major_name
		 	}
		});
	},
																															/**
	 * 获取name_of_institution
	 */
	get_tuition_payment_name_of_institution(){
		let url = "~/api/tuition_payment/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "name_of_institution": this.query.word }, (json) => {
		  if (json.result) {
			var result_tuition_payment_name_of_institution = json.result.list;
			result_tuition_payment_name_of_institution.map(o => o.title = o['name_of_institution'])
	  			this.result_tuition_payment_name_of_institution = result_tuition_payment_name_of_institution
		 	}
		});
	},
						/**
	 * 获取major_name
	 */
	get_tuition_payment_major_name(){
		let url = "~/api/tuition_payment/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "major_name": this.query.word }, (json) => {
		  if (json.result) {
			var result_tuition_payment_major_name = json.result.list;
			result_tuition_payment_major_name.map(o => o.title = o['major_name'])
	  			this.result_tuition_payment_major_name = result_tuition_payment_major_name
		 	}
		});
	},
																/**
	 * 获取student_name
	 */
	get_international_student_management_student_name(){
		let url = "~/api/international_student_management/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "student_name": this.query.word }, (json) => {
		  if (json.result) {
			var result_international_student_management_student_name = json.result.list;
			result_international_student_management_student_name.map(o => o.title = o['student_name'])
	  			this.result_international_student_management_student_name = result_international_student_management_student_name
		 	}
		});
	},
									/**
	 * 获取student_nationality
	 */
	get_international_student_management_student_nationality(){
		let url = "~/api/international_student_management/get_list?like=0";
				this.$get(url, { page: 1, size: 10, "student_nationality": this.query.word }, (json) => {
		  if (json.result) {
			var result_international_student_management_student_nationality = json.result.list;
			result_international_student_management_student_nationality.map(o => o.title = o['student_nationality'])
	  			this.result_international_student_management_student_nationality = result_international_student_management_student_nationality
		 	}
		});
	},
															
  },
  components: { list_result_search },
	created(){
    this.query.word = this.$route.query.word || "";
  },
  mounted() {
	this.get_article();
					this.get_student_users_student_name();
										this.get_student_users_student_nationality();
								this.get_application_guide_guide_title();
							this.get_application_guide_guide_type();
																	this.get_about_us_article_title();
							this.get_about_us_article_type();
														this.get_professional_information_name_of_institution();
							this.get_professional_information_major_name();
																										this.get_application_for_studying_abroad_name_of_institution();
							this.get_application_for_studying_abroad_major_name();
																																this.get_tuition_payment_name_of_institution();
							this.get_tuition_payment_major_name();
																	this.get_international_student_management_student_name();
										this.get_international_student_management_student_nationality();
															  },
  watch: {
	$route() {
	  $.push(this.query, this.$route.query);
	  this.get_article();
				  this.get_student_users_student_name();
									  this.get_student_users_student_nationality();
							  this.get_application_guide_guide_title();
						  this.get_application_guide_guide_type();
																  this.get_about_us_article_title();
						  this.get_about_us_article_type();
													  this.get_professional_information_name_of_institution();
						  this.get_professional_information_major_name();
																									  this.get_application_for_studying_abroad_name_of_institution();
						  this.get_application_for_studying_abroad_major_name();
																															  this.get_tuition_payment_name_of_institution();
						  this.get_tuition_payment_major_name();
																  this.get_international_student_management_student_name();
									  this.get_international_student_management_student_nationality();
																},
  },
};
</script>

<style scoped>
.card_search {
  text-align: center;
}
.card_result_search>.title {
  text-align: center;
  padding: 10px 0;
}
</style>
